'use client';
import React from 'react';

export default function CompletedOrders() {
  const completedOrders = [
    { id: 1, customer: 'Alice Smith', total: '$300.00', date: '2025-01-01', items: 5 },
    { id: 2, customer: 'David Johnson', total: '$450.00', date: '2025-01-03', items: 8 },
    { id: 3, customer: 'Emma Davis', total: '$220.00', date: '2025-01-06', items: 3 },
    { id: 4, customer: 'Oliver Wilson', total: '$540.00', date: '2025-01-08', items: 10 },
  ];

  return (
    <section className="p-6 bg-gray-50 rounded-lg">
      <h2 className="text-2xl font-semibold text-gray-700 mb-6">Completed Orders</h2>
      <div className="overflow-x-auto">
        <table className="w-full table-auto border-collapse bg-white rounded-lg shadow-sm overflow-hidden">
          <thead>
            <tr className="bg-gray-100 text-gray-600 text-sm leading-6">
              <th className="px-4 py-3 font-medium text-left">Order ID</th>
              <th className="px-4 py-3 font-medium text-left">Customer</th>
              <th className="px-4 py-3 font-medium text-left">Total</th>
              <th className="px-4 py-3 font-medium text-left">Items</th>
              <th className="px-4 py-3 font-medium text-left">Date</th>
            </tr>
          </thead>
          <tbody>
            {completedOrders.map((order, index) => (
              <tr
                key={order.id}
                className={`${
                  index % 2 === 0 ? 'bg-gray-50' : 'bg-white'
                } hover:bg-gray-100 transition-colors`}
              >
                <td className="px-4 py-3 text-sm font-semibold text-gray-700 whitespace-nowrap">
                  #{order.id}
                </td>
                <td className="px-4 py-3 text-sm text-gray-600 whitespace-nowrap">
                  {order.customer}
                </td>
                <td className="px-4 py-3 text-sm font-bold text-gray-800 whitespace-nowrap">
                  {order.total}
                </td>
                <td className="px-4 py-3 text-sm text-gray-500 ">
                  {order.items}
                </td>
                <td className="px-4 py-3 text-sm text-gray-500 whitespace-nowrap">
                  {order.date}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </section>
  );
}
