'use client';
import React from 'react';

export default function AllOrders() {
  const orders = [
    { id: 1, customer: 'John Doe', total: '$120.00', status: 'Pending', date: '2025-01-01' },
    { id: 2, customer: 'Jane Smith', total: '$250.00', status: 'Completed', date: '2025-01-02' },
    { id: 3, customer: 'Robert Johnson', total: '$90.00', status: 'Cancelled', date: '2025-01-03' },
    { id: 4, customer: 'Emily Davis', total: '$300.00', status: 'Completed', date: '2025-01-04' },
    { id: 5, customer: 'Michael Brown', total: '$150.00', status: 'Pending', date: '2025-01-05' },
    { id: 6, customer: 'Sarah Wilson', total: '$80.00', status: 'Cancelled', date: '2025-01-06' },
  ];

  return (
    <section className="p-6 bg-gray-50 rounded-lg shadow-md mt-6">
      <h2 className="text-xl font-semibold text-gray-800 mb-4">All Orders</h2>
      <div className="overflow-x-auto">
        <table className="w-full text-left bg-white rounded-lg shadow-lg">
          <thead className="bg-gray-100 text-gray-600 text-sm">
            <tr>
              <th className="px-4 py-3 font-medium">Order ID</th>
              <th className="px-4 py-3 font-medium">Customer</th>
              <th className="px-4 py-3 font-medium">Total</th>
              <th className="px-4 py-3 font-medium">Status</th>
              <th className="px-4 py-3 font-medium">Date</th>
            </tr>
          </thead>
          <tbody>
            {orders.map((order, index) => (
              <tr
                key={order.id}
                className={`${
                  index % 2 === 0 ? 'bg-gray-50' : 'bg-white'
                } hover:bg-gray-200 transition-colors`}
              >
                <td className="px-4 py-3 text-sm font-semibold text-gray-700">#{order.id}</td>
                <td className="px-4 py-3 text-sm text-gray-600">{order.customer}</td>
                <td className="px-4 py-3 text-sm font-bold text-gray-800">{order.total}</td>
                <td
                  className={`px-4 py-3 text-sm font-medium ${
                    order.status === 'Pending'
                      ? 'text-yellow-600'
                      : order.status === 'Completed'
                      ? 'text-green-600'
                      : order.status === 'Cancelled'
                      ? 'text-red-600'
                      : 'text-gray-600'
                  }`}
                >
                  {order.status}
                </td>
                <td className="px-4 py-3 text-sm text-gray-500">{order.date}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </section>
  );
}


