'use client';
import React, { useState } from 'react';
import AllOrders from '@/components/orders/all-orders';
import PendingOrders from '@/components/orders/pending';
import CompletedOrders from '@/components/orders/completed-orders';
import OrderHistory from '@/components/orders/order-hstory';

const tabs = [
  { id: 'all', label: 'All Orders' },
  { id: 'pending', label: 'Pending Orders' },
  { id: 'completed', label: 'Completed Orders' },
  { id: 'history', label: 'Order History' },
];

export default function OrderDashboard() {
  const [activeTab, setActiveTab] = useState('all');

  const renderTabContent = () => {
    switch (activeTab) {
      case 'pending':
        return <PendingOrders />;
      case 'completed':
        return <CompletedOrders />;
      case 'history':
        return <OrderHistory />;
      default:
        return <AllOrders />;
    }
  };

  return (
    <div className="min-h-screen bg-gray-100">
      {/* Header */}
      <div className="bg-white shadow mb-6">
        <div className="container mx-auto px-4 py-4">
          <h1 className="text-3xl font-bold text-center text-gray-800">Order Dashboard</h1>
        </div>
      </div>

      {/* Main Content */}
      <div className="container mx-auto px-4">
        {/* Tab Navigation */}
        <div className="flex flex-wrap justify-center gap-4 mb-6">
          {tabs.map((tab) => (
            <button
              key={tab.id}
              role="tab"
              aria-selected={activeTab === tab.id}
              aria-controls={`tab-panel-${tab.id}`}
              className={`px-4 py-2 text-sm font-medium rounded transition-all duration-200 ${
                activeTab === tab.id
                  ? 'bg-blue-600 text-white shadow-md'
                  : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
              }`}
              onClick={() => setActiveTab(tab.id)}
            >
              {tab.label}
            </button>
          ))}
        </div>

        {/* Tab Content */}
        <div
          id={`tab-panel-${activeTab}`}
          role="tabpanel"
          className="bg-white rounded-lg shadow p-6"
        >
          {renderTabContent()}
        </div>
      </div>
    </div>
  );
}
