'use client';
import React from 'react';

export default function PendingOrders() {
  const pendingOrders = [
    { id: 1, customer: 'John Doe', total: '$120.00', date: '2025-01-01' },
    { id: 2, customer: 'Michael Brown', total: '$150.00', date: '2025-01-05' },
    { id: 3, customer: 'Emily Clark', total: '$200.00', date: '2025-01-07' },
  ];

  return (
    <section className="p-6 bg-gray-50 rounded-lg shadow-md">
      <h2 className="text-xl font-semibold text-gray-800 mb-4">Pending Orders</h2>
      <div className="overflow-x-auto">
        <table className="w-full text-left bg-white rounded-lg shadow-lg">
          <thead className="bg-gray-100 text-gray-600 text-sm">
            <tr>
              <th className="px-4 py-3 font-medium">Order ID</th>
              <th className="px-4 py-3 font-medium">Customer</th>
              <th className="px-4 py-3 font-medium">Total</th>
              <th className="px-4 py-3 font-medium">Date</th>
            </tr>
          </thead>
          <tbody>
            {pendingOrders.map((order, index) => (
              <tr
                key={order.id}
                className={`${
                  index % 2 === 0 ? 'bg-gray-50' : 'bg-white'
                } hover:bg-gray-200 transition-colors`}
              >
                <td className="px-4 py-3 text-sm font-semibold text-gray-700">#{order.id}</td>
                <td className="px-4 py-3 text-sm text-gray-600">{order.customer}</td>
                <td className="px-4 py-3 text-sm font-bold text-gray-800">{order.total}</td>
                <td className="px-4 py-3 text-sm text-gray-500">{order.date}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </section>
  );
}
